import request from '../utils/request.js'
// 
export const login =(data,header) => {
	// console.log()
	return request({
		url:'/auth/system/idcardLogin',
		method:'post',
		data:data,
		header:header
	})
};
// 
export const logout =(data) => {
	// console.log()
	return request({
		url:'/auth/system/logout',
		method:'post',
		data:data,
	})
};
