// 
export const getMsgList =(data) => {
	// console.log()
	return request({
		url:'/auth/system/queueMessage/receiveList',
		method:'get',
		params:{
			page:1,
			pageSize:20,
			read_status:'all'
		},
		data:data,
	})
};
// 
export const delMsg =(data) => {
	// console.log()
	return request({
		url:'/auth/system/queueMessage/deletes/'+data.id,
		method:'delete',
		data:data,
	})
};
